

.hero-container
 {
    display: flex;
align-items: center;
 position: relative;
 }

 .hero-container::after,
 .hero-container::before{

    content: "";
    width: 28.125rem;
    height: 28.125rem;
    border-radius: 28.125rem;
    background: #7c66e3;
    position: absolute;
    z-index:-1 ;
    filter: blur(225px);
 }

 .hero-container::after{
    top: -3rem;
    left: -3rem;

 }

 .hero-container::before{
    bottom: 2rem;
    right:0rem ;
 }
